.js-loader {
  width: 24px;
  height: 24px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #130770;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  font-family: Roboto, sans-serif;
}

img {
  display: block;
}

.breed-select {
  margin-bottom: 15px;
}

.cat-info {
  display: flex;
}

.cat-description {
  width: 600px;
  margin-left: 15px;
}

.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

